html,
body,
#root {
	margin: 0;
	padding: 0;
	width: 100%;
	height: 100%;
}

body {
	background-color: #222;
}

.App {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	width: 100%;
	height: 100%;
}

.Color {
	width: 100%;
	height: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
}

.BatteryCharging {
	-webkit-animation: charging 1s alternate infinite;
	        animation: charging 1s alternate infinite;
}

.BatteryDischarging {
	-webkit-animation: discharging 1s alternate infinite;
	        animation: discharging 1s alternate infinite;
}

.BatteryUnknown {
	color: #ddd;
}

.BatteryText {
	font-size: 40vmin;
	font-family: Arial, sans-serif;
}

@-webkit-keyframes charging {
	from { -webkit-transform: scale(1); transform: scale(1); }
	to { -webkit-transform: scale(1.2); transform: scale(1.2); }
}

@keyframes charging {
	from { -webkit-transform: scale(1); transform: scale(1); }
	to { -webkit-transform: scale(1.2); transform: scale(1.2); }
}

@-webkit-keyframes discharging {
	/* No animation */
}

@keyframes discharging {
	/* No animation */
}



